/*defined variable*/
$colorPrimary: #222;
$colorFont: #5AC7C7;
$colorPinkLight: #d10f71;
$colorWhite: #fff;
$colorLine: #535353;

.game {

    ul,
    li {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    ul {
        padding-top: 10px;
    }

    li {
        display: flex;
        flex-direction: row;
        padding-right: 15px;
        border-bottom: 1px solid $colorLine;

        .photo {
            width: 100px;
            height: 110px;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }

        .content {
            display: flex;
            flex-grow: 1;
            -webkit-align-items: center;
            align-items: center;
            padding: 8px 10px;
            word-break: break-all;

            h1 {
                font-size: 1.375em;
                color: $colorFont;
            }
        }

        .buttons {
            display: flex;
            flex-direction: column;
            -webkit-align-items: center;
            align-items: center;
            -webkit-justify-content: center;
            justify-content: center;

            .start {
                padding: 0 12px;
                background: $colorPinkLight;
                border-color: $colorPinkLight;
            }
        }
    }
}